*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    background: #f1f5f8;
    
}
.mosescv_container h2{
    visibility: hidden;
    font-size: 0.5px;
}
.mosescv_container{
   display:  flex; 
   flex-direction: row;
   border: #6d7880 solid ;
   padding: 2rem;
   margin: 2rem 5rem;
   border-radius: 3rem;
}
.mosescv_information{
    display: flex;
    flex-direction: column;
    margin-left: 2rem;
    margin-right: 3rem;
    width: 100% ;   
}
 img{
    width: 100%;
    height:300px;
    border-radius: 2rem;
}
.mosescv_detail{
    display: flex;
    flex-direction: column;
    width: 100% auto;
    margin-left: 4rem;
    margin-right: 0;
}
@media screen and (max-width:650px){
    .mosescv_container{
        border: none;
        margin: 0.5rem 0.5rem;
    }
    img{
        width: 60%;
        height: 150px;
        border-radius: 1rem;
    }
    .mosescv_information{
      margin-left: 1rem;
      margin-right: 1rem;
      width: 100%;
    }
    .mosescv_detail{
        margin-left: 1.5rem;

    }
    .mosescv_container{
       flex-direction: column;
    }
    .mosescv_container h2{
        text-align: center;
        visibility: visible;
        font-size: 20px;
        color: var(--color-bg);
        line-height: 2rem;
        margin-top: 0.5rem;
    }
}

